/*通用的style*/
body {
  margin: 0;
  width: 100%;
  height: 100%;
}
.hidden {
  display: none;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.mb0 {
  margin-bottom: 0 !important;
}
.mt5 {
  margin-top: 5px;
}
.mb5 {
  margin-bottom: 5px;
}
.w240 {
  width: 240px;
}
.query-form {
  margin: 0 0 20px;
  background: #EBEEF5;
}
.query-form .el-card__body {
  padding: 20px 20px 0;
}
.el-dialog {
  min-width: 490px !important;
  overflow: auto;
}
.el-input-number .el-input__inner {
  text-align: left;
}
.table-expand {
  font-size: 0;
}
.table-expand label {
  width: 90px;
  color: #99a9bf;
}
.table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.avatar-uploader {
  position: relative;
}
.avatar-uploader .avatar {
  width: 120px;
  height: 120px;
  display: block;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.avatar-uploader .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}
.avatar-uploader .upload-progress {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #000;
  font-size: 16px;
  font-weight: bold;
}
.el-select-dropdown__item {
  max-width: 350px;
  padding: 0 27px 0 20px;
}
.el-select__tags .el-select__tags-text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 150px;
  display: inline-block;
  vertical-align: middle;
}
.el-select__tags .el-tag {
  line-height: normal;
}
.el-select__tags .el-tag .el-icon-close {
  top: 2px;
}
.el-menu--collapse .el-submenu > .el-submenu__title > span {
  height: 0;
  width: 0;
  overflow: hidden;
  visibility: hidden;
  display: inline-block;
}
.el-menu--collapse .el-submenu > .el-submenu__title .el-submenu__icon-arrow {
  visibility: hidden;
}
/*清除浮动*/
.clear:after {
  clear: both;
  display: table;
  content: "";
}
/*IE浏览器的专有属性，触发ie的hasLayout属性，清除浮动*/
.clear {
  *zoom: 1;
}
.el-table a {
  color: #409eff;
  text-decoration: none;
}
.widthCustom {
  width: 160px;
}
.iconfont {
  display: inline-block;
}
/* 嵌套表格 */
.nestingTable {
  overflow: hidden;
}
.nestingTable .el-table__expanded-cell,
.nestingTable .el-table__expanded-cell[class*=cell] {
  padding: 0;
}
.nestingTable .el-table {
  border: none;
  overflow: hidden;
}
.nestingTable .el-table tr:nth-last-child(1) td {
  border-bottom: none;
}
.nestingTable .el-table__row.noChildren .el-table__expand-icon {
  visibility: hidden;
}
.nestingTable .hasChildren + tr .el-table__expanded-cell {
  border: none;
}
.nestingTable tr .el-table__body-wrapper {
  overflow-x: hidden;
}
/* 嵌套表格 end */
.el-notification__content {
  font-size: 14px;
  line-height: 21px;
  margin: 6px 0 0;
  color: #606266;
  text-align: inherit;
  word-break: break-all;
}
button .iconfont {
  font-size: 12px;
  margin-right: 5px;
}
