
body {
  background: #fff;
}
.layout {
  background: #fff;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-width: 1200px;
}
.layout .layout-content-card {
  margin: 5px;
  padding: 10px 20px 20px;
  background: #fff;
}
.layout .layout-content {
  min-height: 500px;
  margin: 15px 0;
}
.layout .layout-copy {
  text-align: center;
  padding: 10px 0 20px;
  color: #9ea7b4;
}
.layout .layout-menu-left:not(.el-menu--collapse) {
  width: 200px;
}
.layout .layout-menu-left.el-menu--collapse {
  overflow: inherit;
}
.layout .layout-menu-left {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  min-height: 100%;
  z-index: 10;
  overflow-y: auto;
  left: 0;
  top: 0;
  bottom: 0;
  opacity: 1;
  -webkit-box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 0 0 0px rgba(0, 0, 0, 0.12);
          box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 0 0 0px rgba(0, 0, 0, 0.12);
  border-right: 0 solid transparent;
  padding-top: 70px;
}
.layout .layout-menu-left::-webkit-scrollbar {
  display: none;
}
.layout .layout-menu-left .el-menu-item-group__title {
  padding: 0;
}
.layout .layout-menu-left .el-submenu [class^=iconfont] {
  vertical-align: middle;
  margin-right: 5px;
  width: 24px;
  text-align: center;
  font-size: 18px;
}
.layout .layout-menu-left .el-menu-item [class^=iconfont] {
  margin-right: 5px;
  width: 24px;
  text-align: center;
  font-size: 18px;
}
.layout .layout-right {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left: 200px;
  -webkit-transition: padding 0.3s ease-in-out;
  transition: padding 0.3s ease-in-out;
}
.layout .layout-right.collapse {
  padding-left: 64px;
}
.layout .layout-right.collapse .nav-control {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.layout .layout-right .nav-control {
  cursor: pointer;
  color: #fff;
  margin: 21px 0 0 24px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  width: 28px;
}
.layout .layout-header {
  height: 70px;
  background: #62abef;
  width: 100%;
}
.layout .layout-header-info {
  float: right;
  margin-right: 4.5%;
  line-height: 70px;
  font-size: 16px;
}
.layout .layout-header-info span {
  color: #fff;
}
.layout .layout-header-info .el-button--text {
  color: #fff;
  margin-left: 10px;
}
.layout .layout-logo {
  height: 70px;
  line-height: 70px;
  text-align: center;
  font-size: 18px;
  color: #fff;
  width: 200px;
  position: fixed;
  z-index: 11;
  top: 0;
  left: 0;
  -webkit-transition: width 0.3s ease-in-out;
  transition: width 0.3s ease-in-out;
  opacity: 1;
  background: #62abef;
  -webkit-box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0 0 0 0 0;
          box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0 0 0 0 0;
}
.layout .layout-logo.collapse {
  width: 64px;
}
.layout .layout-logo.collapse img {
  width: 60px;
}
.layout .layout-logo a {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  height: 70px;
}
.layout .layout-logo img {
  margin: 23px 0 0 0;
  width: 134px;
  -webkit-transition: width 0.28s ease-out;
  transition: width 0.28s ease-out;
}
.layout .el-breadcrumb__item__inner:hover,
.layout .el-breadcrumb__item__inner a:hover {
  color: #48576a;
  cursor: text;
}
.layout .el-breadcrumb {
  padding: 10px 0;
  border-bottom: 1px solid #eaeefb;
}
.layout .header-menu {
  display: inline-block;
  vertical-align: top;
  height: 70px;
  line-height: 70px;
}
.layout .header-menu ul {
  margin: 0 0 0 10px;
  padding: 0;
}
.layout .header-menu ul li {
  display: inline-block;
  padding: 0 20px;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}
.layout .header-menu ul li.active {
  background: #5a9bd7;
}
